
/* LEARN HOW TO HIDE BORDER OVERFLOW .city:nth-child(7) */

*, *:before, *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}
a {
  color: white;
}

.vh-100 {
  height: 100vh;
}


.flex-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center
}

.flex-column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.containerone {
  width: 87vw;
}

.landing-box {
height: 100%;
width: 100%;
padding: 4%;
background-color: #292929;
color: #fbffe2;
}

.landing {
  min-height: 65%;
  min-width: 75%;
  -webkit-transition-duration: 0.7s;
       -o-transition-duration: 0.7s;
          transition-duration: 0.7s;
  background-size: cover;
  -ms-flex-positive: 65;
      flex-grow: 65; 
}

.logo-text, {
  height: 4%;
  -ms-flex-positive: 5;
      flex-grow: 5;
  margin: 0.3%;
}

.landing-text {
  text-align: center;
  margin: 0.5% auto;
  font-family: 'Inconsolata', monospace;
  font-weight: 100;
  font-size: 0.8em;
}

.learnmore {
  min-width: 40%;
  background-color: #1c1c1c;
  border: none;
  font-size: 1em;
  font-family: 'Inconsolata', monospace;
  text-align: center;
  padding: 0.5% 4%;
  margin-bottom: 3%;
}

.learnmore:hover {
  background-color: #fbffe2;
  -webkit-transition-property: background-color, text-align;
  -o-transition-property: background-color, text-align;
  transition-property: background-color, text-align;
  color: black;
  cursor: pointer;
}

.cities {
  background-color: black;
  position: fixed;
  opacity: 0.8;
  top: 0;
  right: 0;
  width: 13%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Inconsolata', monospace;
  font-size: 1em;
}

.city {
  border: 1px solid #fbffe2;
  text-align: right;
  height: 14.3%;
  padding: 10% 6% 3% 3%;
  overflow: hidden;
  font-size: 0.8em;
  color: white;
}

.city:hover {
  background-color: #fbffe2;
  color: black;
  text-align: center;
  -webkit-transition-property: text, background-color;
  -o-transition-property: text, background-color;
  transition-property: text, background-color;
  -webkit-transition-duration: 1.2s;
       -o-transition-duration: 1.2s;
          transition-duration: 1.2s;
}
.selected-city {
  background-color: #fbffe2;
  color: black;
  text-align: center;
  overflow: hidden;
}



.containertwo {
  width: 87vw;
  overflow: hidden;
}

.phone-container {
  width: 471px;
  height: 844px;
  overflow: hidden;
  display: inline-block;
  border: 1px solid black; 
}

.flex-row button {
  height: 20vh;
  width: 10vh;
  font-size: 5em;
  background-color: white;
  border: none;
}

.flex-row button:hover {
  background: #dbdbdb;
  -webkit-transition-duration: 1.5s;
       -o-transition-duration: 1.5s;
          transition-duration: 1.5s;
}

.left-arrow, .right-arrow {
  width: 20%;
  display: inline-block;
  text-align: center;
}

.right-arrow:hover, .left-arrow:hover, button, .city, .phone-container:hover {
  cursor: pointer;
}

.car-header {
  color: #303030;
  font-family: 'Courier New', monospace;
  text-align: center;
}



@media (max-width: 860px) {
  .city, .landing-text {
    font-size: 0.75em;
  }

  .learnmore {
    font-size: 1.2em;
  }
  
  .left-arrow, .right-arrow {
    display: none;
    max-width: 0;
  }

  .cities {
    position: fixed;
    width: 25vw;
  }

  .containertwo, .containerone {
    width: 75%;
  }
}

@media (max-width: 500px) {
  .phone-container {
    width: 300px;
    height: 535px;
  }
}


